import React from "react"
import { Alert, Button } from "antd"
import { ArrowLeftOutlined } from "@ant-design/icons"

import CopyableText from "../components/copyable-text"
import LinkButton from "../components/link-button"
import AirbnbLogo from "../assets/airbnb-logo"

const styles = {
  div: {
    maxWidth: "320px",
  },
  alert: {
    fontSize: "16px",
    marginBottom: "32px",
    borderRadius: "8px"
  },
  section: {
    marginBottom: "32px",
    display: "flex",
    justifyContent: "space-between"
  }
}
const dateFormat = "MD"

const requestTemplate = (reason, numDays, startDate, endDate) => (
  `${reason}${numDays}${startDate.format(dateFormat)}${endDate.format(dateFormat)}
            `
)

const responseTemplate = (startDate, endDate, contactName, contactMethod) => (
  `${startDate.format(dateFormat)}${endDate.format(dateFormat)}${contactName}${contactMethod}`)

export default function({ form, onBack }) {

  const reason = form.getFieldValue("reason")
  const startDate = form.getFieldValue("startDate")
  const endDate = form.getFieldValue("endDate")

  const numDays = endDate.diff(startDate, "days")

  const contactName = form.getFieldValue("contactName")
  const contactMethod = form.getFieldValue("contactMethod")

  const request = requestTemplate(reason, numDays, startDate, endDate)
  const response = responseTemplate(startDate, endDate, contactName, contactMethod)

  return (
    <div style={styles.div} className="results">
      <Alert message='' type='info' style={styles.alert}/>
      <CopyableText title="" text={request}/>
      <CopyableText title="" text={response}/>
      <section style={styles.section}>
        <Button onClick={onBack} type='primary' size='large' icon={<ArrowLeftOutlined/>}></Button>
        <LinkButton to='https://www.airbnb.com/' className='airbnb-btn' icon={<AirbnbLogo/>}></LinkButton>
      </section>
    </div>
  )
}
