import React from 'react';
import { Form, Divider, Button } from 'antd';
import { TeamOutlined, MailOutlined, ArrowRightOutlined } from '@ant-design/icons';

import Input from '../components/input';
import Select from '../components/select';
import RangePicker from '../components/range-picker';
import DatePicker from '../components/date-picker';

const styles = {
    form: {
        maxWidth: "320px"
    },
    icon: {
        color: "rgba(0, 0, 0, 0.25)",
    }
}

const shouldUpdate = (prev, current) => {
    return (prev.startDate !== current.startDate || prev.endDate !== current.endDate);
};

const FormItem = ({ name, message, children, ...otherProps }) => {

    return (
        <Form.Item
            name={name}
            gutter={[0, 40]}
            rules={[{ required: true, message }]}
            {...otherProps}
        >
            {children}
        </Form.Item>
    )
}

export default function ({ form, onFinish, onRangeChange, onDateChange }) {

    return (
        <Form
            form={form}
            onFinish={onFinish}
            layout="vertical"
            className="inputs"
            style={styles.form}
        >
            <Divider></Divider>
            <FormItem name="reason" message="">
                <Select
                    placeholder=""
                    form={form}
                />
            </FormItem>
            <FormItem
                name="range"
                message=""
                className="range-picker"
            >
                <RangePicker
                    onChange={onRangeChange}
                    placeholder={['', '']}
                />
            </FormItem>
            <Form.Item
                noStyle
                shouldUpdate={shouldUpdate}
            >
                {({ getFieldValue }) => (<FormItem
                    name="startDate"
                    message=""
                    className="date-picker"
                >
                    <DatePicker
                        variant="startDate"
                        endDate={getFieldValue("endDate")}
                        placeholder=""
                        onDateChange={onDateChange}
                    />
                </FormItem>)}
            </Form.Item>
            <Form.Item
                noStyle
                shouldUpdate={shouldUpdate}
            >
                {({ getFieldValue }) => {
                    return (
                        <FormItem
                            message=""
                            className="date-picker"
                            name="endDate"
                        >
                            <DatePicker
                                variant="endDate"
                                startDate={getFieldValue("startDate")}
                                placeholder=""
                                onDateChange={onDateChange}
                            />
                        </FormItem>
                    );
                }}
            </Form.Item>
            <Divider></Divider>
            <FormItem
                name="contactName"
                message="">
                <Input
                    placeholder=""
                    icon={<TeamOutlined style={styles.icon}/>}/>
            </FormItem>

            <FormItem name="contactMethod"
                      message="">
                <Input
                    placeholder=""
                    icon={<MailOutlined style={styles.icon}/>}/>
            </FormItem>
            <div style={{ textAlign: "right", width: "100%", paddingBottom: "40px"}}>
                <Button
                    type="primary"
                    htmlType="submit"
                    size="large"> <ArrowRightOutlined/></Button>
            </div>
        </Form>
    );
}
